<template>
  <div class="content_film">
    <div class="header">
      <i class="iconfont icon1" @click="$router.push('/main/cinema')">&#xe647;</i>
      <p class="zero"></p>
      <van-sticky :offset-top="0">
        <p class="title" v-text="cinema.name"></p>
      </van-sticky>
      <ul class="ul1">
        <li v-for="value in cinema.services" v-text="value.name" @click="$router.push('/main/cinema/'+$route.params.id)"></li>
        <li class="li_i"><i class="iconfont icon2">&#xe643;</i></li>
      </ul>
      <div class="address">
        <i class="iconfont">&#xe601;</i>
        <p v-text="cinema.address"></p>
        <i class="iconfont icon3">&#xe953;</i>
      </div>
    </div>
    <div class="body">
      <div :class="i===index?'fil':'fil fil_to'" :style="change(i)" v-for="(value,i) in films"></div>
      <div class="imgs">
        <swiper :key="films.length">
          <div class="swiper-slide" v-for="value in films">
            <img :src="value.poster" alt="" class="swiper_img">
          </div>
        </swiper>
      </div>
      <div class="sj">
        <img src="/sj.png" alt="">
      </div>
    </div>
    <van-sticky :offset-top="44">
      <div class="footer" v-if="films.length!=0">
        <div class="film" @click="$router.push(`/main/movie/on/detail/${$route.params.filmid}`)">
          <div>
            <p>{{ films[index].name }}<span>{{ films[index].grade }}</span><span class="scol">分</span></p>
            <p>{{ films[index].category }} | {{ films[index].runtime }}分钟 | {{ films[index].director }} |
              {{ films[index].actors | otor }}</p>
          </div>
          <i class="iconfont icon4">&#xe643;</i>
        </div>
        <div class="timer">
          <ul>
            <router-link :to="url[index]" v-for="(value,index) in films[index].showDate" replace custom
                         v-slot="{navigate,isActive}">
              <li @click="navigate" :class="isActive?'active':''" :key="Math.random()*100">
                <div @click="data_go(value)">{{ date(value) }}</div>
              </li>
            </router-link>
          </ul>
        </div>
      </div>
    </van-sticky>
    <div class="value">
      <router-view :datalist="datalist===undefined?[]:datalist"></router-view>
    </div>
  </div>
</template>

<script>
  import Vue from 'vue';
  import {Sticky, Toast, Tab, Tabs} from 'vant';

  import axios from "axios";
  import swiper from "@/components/Swipermy.vue"

  Vue.use(Toast);
  Vue.filter("otor", (value) => {
    let arr = "";
    value.forEach(v => {
      arr += v.name + " "
    })
    return arr
  })
  Vue.use(Sticky);
  export default {
    data() {
      return {
        url: [],
        cinema: [],
        films: [],
        index: 0,
        scroll: 0,
        //详情页面
        cache: {},
        status: [],
        statusn: 0,
        active: 0,
        datalist: []
      }
    },
    components: {
      swiper
    },
    created() {
      this.axios1();
      this.axios2();
      this.$watch(() => {
        return this.$store.state.index
      }, () => {
        this.index = this.$store.state.index
      })
    },
    mounted() {

    },
    updated() {

    },
    methods: {
      axios1() {
        return axios({
          url: `https://m.maizuo.com/gateway/?cinemaId=${this.$route.params.id}&k=993360`,
          headers: {
            'X-Client-Info': '{"a":"3000","ch":"1002","v":"5.0.4","e":"16357749354350673021829121","bc":"130900"}',
            'X-Host': 'mall.film-ticket.cinema.info'
          }
        }).then(value => {
          this.cinema = value.data.data.cinema
        })
      },
      axios2() {
        return axios({
          url: `https://m.maizuo.com/gateway/?cinemaId=${this.$route.params.id}&k=5234719`,
          headers: {
            'X-Client-Info': ' {"a":"3000","ch":"1002","v":"5.0.4","e":"16357749354350673021829121","bc":"130900"}',
            'X-Host': 'mall.film-ticket.film.cinema-show-film'
          }
        }).then(value => {
          let arr = []
          value.data.data.films.forEach(v => {
            arr.push({filmId: v.filmId, time: v.showDate})
          })
          this.films = value.data.data.films
          this.bgurl = `background: url("${this.films[this.index].poster}")`
          Toast.loading({
            message: 'loading...',
            forbidClick: true,
            duration: 0
          });
          this.axios3(this.films[this.index].filmId, this.films[this.index].showDate[0]).then(v => {
            this.datalist = v
            Toast.clear()
          })
          this.on(arr)
        })
      },
      //缓存树填充
      axios3(id, time) {
        return axios({
          url: `https://m.maizuo.com/gateway/?filmId=${id}&cinemaId=${this.$route.params.id}&date=${time}&k=5021935`,
          headers: {
            'X-Client-Info': '{"a":"3000","ch":"1002","v":"5.0.4","e":"16357749354350673021829121","bc":"130900"}',
            'X-Host': 'mall.film-ticket.schedule.list'
          }
        })
          .then(value => {
            if (value.data.data) {
              this.status.push(true)
              return value.data.data.schedules
            }
          })
      },
      change(index) {
        return `background: url("${this.films[index].poster}")`;
      },
      date(value) {
        let t = new Date(value * 1000);
        let arr = ["今天", "明天", "后天"];
        let week = ["周天", "周一", "周二", "周三", "周四", "周五", "周六"];
        let str = ""
        if (t.getDate() - new Date().getDate() < 0 || t.getDate() - new Date().getDate() > 2) {
          str = week[t.getDay()]
        } else {
          str = arr[t.getDate() - new Date().getDate()]
        }
        return str + (t.getMonth() + 1) + "月" + t.getDate() + "日";
      },
      data_to() {
        let arr = {}
        for (let i = 0; i < this.films[this.index].showDate.length; i++) {
          arr[i] = (`/main/cinema/${this.$route.params.id}/film/${this.films[this.index].filmId}/${this.films[this.index].showDate[i]}`)
        }
        this.url = arr
      },
      data() {
        this.datalist = this.cache[this.films[this.index].filmId][this.films[this.index].showDate[0]]
        this.$router.push(`/main/cinema/${this.$route.params.id}/film/${this.films[this.index].filmId}/${this.films[this.index].showDate[0]}`)
      },
      data_go(value) {
        //将数据传给子元素
        this.$router.push(`/main/cinema/${this.$route.params.id}/film/${this.films[this.index].filmId}/${this.films[this.index].showDate[0]}`)
        this.datalist = this.cache[this.films[this.index].filmId][value]
      },
      on(arr) {
        //建立缓存树
        if (arr) {
          arr.forEach((v, i) => {
            this.cache[v.filmId] = {}
            arr[i].time.forEach(async value => {
              this.cache[v.filmId][value] = await this.axios3(v.filmId, value)
              this.statusn++
            })
          })
        }
      },
    },
    watch: {
      films() {
        this.data_to()
        this.data()
      },
      index() {
        this.data()
        this.data_to()
      },
    }
  }
</script>

<style scoped lang="scss">
  .van-tabs, van-tabs--line {
    width: 100% !important;
  }

  .content_film {
    width: 100%;
    background: #fff;
    overflow: hidden;

    .header {
      position: relative;
      width: 100%;

      .icon1 {
        position: fixed;
        top: 0;
        left: 15px;
        height: 44px;
        display: flex;
        align-items: center;
        z-index: 100;
      }

      .zero {
        width: 100%;
        background: #fff;
        height: 44px;
      }

      .title {
        width: 100%;
        font-size: 17px;
        color: #191a1b;
        height: 44px;
        display: flex;
        align-items: center;
        justify-content: center;
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
        background: #fff;
      }

      .ul1 {
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 5px 0 15px;

        li {
          height: 18px;
          border: 1px solid #ffb232;
          color: #ffb232;
          padding: 0 4px;
          margin: 0 2.5px;
          font-size: 10px;
          box-sizing: border-box;
        }

        .li_i {
          border: 0;
          display: flex;
          padding: 0;

          .icon2 {
            margin-top: 1px;
          }
        }
      }

      .address {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding-left: 17px;
        height: 50px;
        border-top: 1px solid #ededed;

        i {
          font-size: 20px;
          color: #c0c0c0;
        }

        p {
          flex: 1;
          padding: 0 12px;
          white-space: nowrap;
          text-overflow: ellipsis;
          overflow: hidden;
          font-size: 14px;
        }

        .icon3 {
          padding: 0 25px;
          border-left: 1px solid #ededed;
          color: #424242;
        }
      }
    }

    .body {
      width: 100%;
      height: 130px;
      padding: 15px 0;
      position: relative;
      overflow: hidden;

      .fil {
        width: 100%;
        height: 130px;
        filter: blur(30px);
        position: absolute;
        top: 15px;
        opacity: 1;
        transition: all 80ms ease-in-out;
      }

      .fil_to {
        width: 100%;
        height: 130px;
        filter: blur(30px);
        position: absolute;
        top: 15px;
        opacity: 0;
      }

      .imgs {
        height: 130px;

        .swiper_img {
          width: 90px;
          height: 130px;
        }
      }

      .sj {
        width: 100%;
        height: 10px;
        position: absolute;
        bottom: 0;
        display: flex;
        justify-content: center;
      }
    }

    .footer {
      background: #fff;

      .timer::-webkit-scrollbar {
        width: 0;
        height: 0;
      }

      .timer {
        width: 100%;
        height: 50px;
        display: flex;
        align-items: center;
        border-bottom: 1px solid #ededed;
        overflow-x: auto;

        .active {
          color: #ff5f16;
          position: relative;
          height: 50px;
        }

        .active::after {
          content: "";
          top: auto;
          bottom: 0;
          height: 2px;
          left: 0;
          right: 0;
          width: 100%;
          background: #ff5f16;
          position: absolute;
        }

        ul {
          width: 0;
          flex: 1;
          display: flex;
          height: 50px;

          li {
            margin: 0 15px;
            font-size: 14px;
            height: 50px;
            display: flex;
            align-items: center;

            div {
              white-space: nowrap;
            }
          }
        }
      }

      .film {
        height: 50px;
        padding: 15px 0;
        position: relative;
        border-bottom: 1px solid #ededed;

        .icon4 {
          position: absolute;
          right: 14px;
          top: 36px;
          font-size: 6px;
        }

        p:nth-of-type(1) {
          margin-bottom: 10px;
          text-align: center;
          line-height: 18px;
          font-size: 15px;

          span:nth-of-type(1) {
            font-size: 16px;
            font-style: italic;
            color: #ffb232;
            margin-left: 5px;
          }

          span:nth-of-type(2) {
            font-size: 10px;
            color: #ffb232;
          }
        }

        p:nth-of-type(2) {
          padding: 0 12%;
          font-size: 13px;
          white-space: nowrap;
          text-overflow: ellipsis;
          overflow: hidden;
          color: #797d82;
        }
      }
    }
  }
</style>

