<template>
  <div class="content">
    <div class="swiper-container">
      <div class="swiper-wrapper">
        <slot></slot>
      </div>
    </div>
  </div>
</template>

<script>
  import Swiper from "swiper/bundle"
  import "swiper/swiper-bundle.css"
  import "swiper/swiper-bundle.js"

  export default {
    mounted() {
      let this_ = this
      let sw = new Swiper('.swiper-container', {
        slidesPerView: 4,
        spaceBetween: 30,
        centeredSlides: true,
        // loop: true,
        pagination: {
          el: '.swiper-pagination',
          clickable: true,
        },
        on: {
          slideChangeTransitionEnd() {
            this_.$store.commit("sw", sw.activeIndex)
          },
        },
      })
    }
  }

</script>

<style scope>
  .swiper-container {
    width: 100%;
    height: 100%;
  }

  .swiper-slide {
    text-align: center;
    font-size: 18px;
    background: inherit;

    /* Center slide text vertically */
    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;
    transition: 300ms;
    transform: scale(0.8);
    transform-origin: bottom center;
  }

  .swiper-slide-active, .swiper-slide-duplicate-active {
    transform: scale(1);
  }
</style>
